@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400&display=swap");

body {
  font-family: "Poppins";
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@media (max-width: 460px) {
  .whatsapp {
    width: 40px;
    position: fixed;
    bottom: 2rem;
    left: 1rem;
    z-index: 100;
  }
  .fcshopimage {
    padding-left: 1rem !important;
    padding-right: 1rem !important;
  }
  .rtail1 {
    margin-left: 0px !important;
  }
}
@media (min-width: 461px) {
  .mm1 {
    padding-left: 2px !important;
    padding-right: 2px !important;
  }
  .fcshop {
    width: 100% !important;
  }
  .maxwidth1 {
    padding-left: 0rem;
  }
  .rtail2 {
    /* max-width: 1400px; */
  }
  .rtail1 {
    font-size: 16px;
    padding: 0px;
  }
}
.App {
  overflow-x: hidden;
}
.mm12 {
  max-width: 916px;
  margin: auto;
}
html {
  overflow-x: hidden;
}
@media (max-width: 1000px) {
  .dashboardmainarea {
    margin-left: 0% !important;
    padding-left: 2rem;
  }
}
@media (max-width: 450px) {
  .card1 {
    margin-right: 0px;
    margin-bottom: 1rem;
  }
  .sidebarUpdate1 {
    flex-wrap: wrap;
  }
  .filter1 {
    margin-left: 1rem;
  }
  .searchz{
    margin-bottom: 1rem;
  }
}
